import $ from 'jquery/dist/jquery.min.js';
import Chart from 'chart.js/dist/Chart.bundle.min.js';

const initChart = () => {
	const $root = $('#chart');
	const chart = new Chart($root, {
		type: 'doughnut',
		data: {
			labels: ['Южная Америка', 'Северная Америка', 'Азия', 'Африка', 'Европа'],
			datasets: [
				{
					data: [520229000, 315915000, 3679737000, 795671000, 727986000],
					backgroundColor: ['#7B669D', '#66AAC3', '#5B80B7', '#D4722E', '#A2B965']
				},
				{
					data: [167097000, 171616000, 1398488000, 221214000, 547403000],
					backgroundColor: ['#AFA3C4', '#A0CADB', '#96B2DF', '#F6D6B8', '#C8D5A2']
				}
			]
		}
	});
}

const initApp = () => {
	$(document).ready(() => {
		initChart();
	});
}

initApp();

